.third-section {
  position: relative;
  background: url("./web-bottle-3-bg@2x.png") no-repeat center/cover; }
  .third-section .third-section--desc {
    width: 70%;
    margin: 0 auto;
    text-align: center; }
    .third-section .third-section--desc .third-section--desc--integration {
      display: inline-block;
      margin-top: 3.8021vw;
      width: 161px;
      height: 12px;
      background: url("./app-integration@2x.png") no-repeat center/contain; }
      .third-section .third-section--desc .third-section--desc--integration h2 {
        opacity: 0;
        overflow: hidden;
        text-indent: -9999px; }
    .third-section .third-section--desc h1 {
      font-size: 2.916vw;
      text-align: center;
      margin-top: 3.2292vw;
      font-weight: 300;
      letter-spacing: -2.6px;
      line-height: 3.5vw; }
    .third-section .third-section--desc p {
      margin-top: 1.5625vw;
      font-size: 1.1458vw;
      color: #8a8a8a;
      line-height: normal;
      text-align: center; }
  .third-section .third-section--progress {
    position: relative;
    display: table;
    margin: 3.7396vw auto 0; }
    .third-section .third-section--progress > div {
