      display: inline-block;
      width: 5.7292vw;
      min-width: 50px;
      z-index: 1; }
    .third-section .third-section--progress .third-section--progress--data {
      position: relative;
      display: inline-block;
      width: 3.0208vw;
      min-width: 29px;
      padding: 0 2.3958vw;
      z-index: 1; }
    .third-section .third-section--progress .horizon-bar {
      position: absolute;
      top: 50%;
      left: 3px;
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
      transform: translateY(-50%);
      display: inline-block;
