  .third-section .third-section--devices {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    text-align: center; }
    .third-section .third-section--devices img {
      display: inline-block; }
    .third-section .third-section--devices img:first-child {
      width: 13.3333vw;
      min-width: 128px; }
    .third-section .third-section--devices img:last-child {
      margin-left: 1.875vw;
      width: 14.5833vw;
      min-width: 140px;
      vertical-align: bottom; }
